/* raleway-regular - latin */
@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 400;
  font-display: swap;

  src: url('../../fonts/raleway-latin-regular.woff2') format('woff2');
}

/* raleway-italic - latin */
@font-face {
    font-family: 'Raleway';
    font-style: italic;
    font-weight: 400;
    font-display: swap;

    src: url('../../fonts/raleway-latin-italic.woff2') format('woff2');
}

